/**
 * è½¬æ¢å‰ç«¯è¡¨å•æ•°æ®ä¸ºæ•°æ®åº“æ’å…¥æ ¼å¼
 * @param formData å‰ç«¯è¡¨å•æ•°æ®
 * @param fieldMappings å­—æ®µæ˜ å°„é…ç½®
 * @returns æ•°æ®åº“æ’å…¥æ•°æ®
 */
export function transformFormToDatabase(
  formData: any,
  fieldMappings: Record<string, string | ((value: any) => any)>,
): Record<string, any> {
  const result: Record<string, any> = {};

  for (const [formField, dbField] of Object.entries(fieldMappings)) {
    const value = formData[formField];

    if (value !== undefined && value !== null) {
      if (typeof dbField === "function") {
        // è‡ªå®šä¹‰è½¬æ¢å‡½æ•°
        const transformedValue = dbField(value);
        if (transformedValue !== undefined && transformedValue !== null) {
          Object.assign(result, transformedValue);
        }
      } else {
        // ç›´æ¥å­—æ®µæ˜ å°„
        result[dbField] = value;
      }
    }
  }

  return result;
}

/**
 * è½¬æ¢æ•°æ®åº“è¡Œæ•°æ®ä¸ºå‰ç«¯ä¸šåŠ¡å¯¹è±¡
 * @param dbRow æ•°æ®åº“è¡Œæ•°æ®
 * @param fieldMappings å­—æ®µæ˜ å°„é…ç½®
 * @returns å‰ç«¯ä¸šåŠ¡å¯¹è±¡
 */
export function transformDatabaseToModel(
  dbRow: any,
  fieldMappings: Record<string, string | ((value: any) => any)>,
): Record<string, any> {
  const result: Record<string, any> = {};

  for (const [dbField, modelField] of Object.entries(fieldMappings)) {
    const value = dbRow[dbField];

    if (value !== undefined && value !== null) {
      if (typeof modelField === "function") {
        // è‡ªå®šä¹‰è½¬æ¢å‡½æ•°
        const transformedValue = modelField(value);
        if (transformedValue !== undefined && transformedValue !== null) {
          Object.assign(result, transformedValue);
        }
      } else {
        // ç›´æ¥å­—æ®µæ˜ å°„
        result[modelField] = value;
      }
    }
  }

  return result;
}

/**
 * éªŒè¯å¿…éœ€å­—æ®µ
 * @param data è¦éªŒè¯çš„æ•°æ®
 * @param requiredFields å¿…éœ€å­—æ®µåˆ—è¡¨
 * @param entityName å®ä½“åç§°ï¼Œç”¨äºé”™è¯¯æ¶ˆæ¯
 */
export function validateRequiredFields(
  data: any,
  requiredFields: string[],
  entityName = "Entity",
): void {
  const missingFields: string[] = [];

  for (const field of requiredFields) {
    if (!data[field]) {
      missingFields.push(field);
    }
  }

  if (missingFields.length > 0) {
    throw new Error(
      `${entityName} validation failed: missing required fields: ${missingFields.join(", ")}`,
    );
  }
}

/**
 * æ¸…ç†æ•°æ®å¯¹è±¡ï¼Œç§»é™¤ undefined å’Œ null å€¼
 * @param data è¦æ¸…ç†çš„æ•°æ®å¯¹è±¡
 * @returns æ¸…ç†åçš„æ•°æ®å¯¹è±¡
 */
export function cleanData(data: Record<string, any>): Record<string, any> {
  const result: Record<string, any> = {};

  for (const [key, value] of Object.entries(data)) {
    if (value !== undefined && value !== null) {
      result[key] = value;
    }
  }

  return result;
}

/**
 * æ·±åº¦åˆå¹¶å¯¹è±¡
 * @param target ç›®æ ‡å¯¹è±¡
 * @param source æºå¯¹è±¡
 * @returns åˆå¹¶åçš„å¯¹è±¡
 */
export function deepMerge(target: any, source: any): any {
  const result = { ...target };

  for (const key in source) {
    if (source.hasOwnProperty(key)) {
      if (
        typeof source[key] === "object" &&
        source[key] !== null &&
        !Array.isArray(source[key])
      ) {
        result[key] = deepMerge(result[key] || {}, source[key]);
      } else {
        result[key] = source[key];
      }
    }
  }

  return result;
}

/**
 * æ ¼å¼åŒ–é”™è¯¯æ¶ˆæ¯
 * @param error é”™è¯¯å¯¹è±¡
 * @param context ä¸Šä¸‹æ–‡ä¿¡æ¯
 * @returns æ ¼å¼åŒ–çš„é”™è¯¯æ¶ˆæ¯
 */
export function formatErrorMessage(error: any, context?: string): string {
  let message = "An error occurred";

  if (error?.message) {
    message = error.message;
  } else if (typeof error === "string") {
    message = error;
  }

  if (context) {
    message = `${context}: ${message}`;
  }

  return message;
}

/**
 * ç”Ÿæˆæ’åºé¡ºåº
 * ä¸ºæ–°é¡¹ç›®ç”Ÿæˆåˆé€‚çš„æ’åºé¡ºåºå€¼
 * @param existingItems ç°æœ‰é¡¹ç›®åˆ—è¡¨
 * @param sortField æ’åºå­—æ®µå
 * @returns æ–°çš„æ’åºé¡ºåºå€¼
 */
export function generateSortOrder(
  existingItems: any[],
  sortField = "sortOrder",
): number {
  if (!existingItems || existingItems.length === 0) {
    return 0;
  }

  const maxOrder = Math.max(
    ...existingItems.map((item) => item[sortField] || 0),
  );

  return maxOrder + 1;
}
