import { supabase, TABLES, handleSupabaseError } from "../lib/supabaseClient";
import { ErrorHandler } from "../utils/errorHandler";
import { apiCache, withCache } from "../utils/cacheManager";
import type { Tool, SearchFilters, SearchResult } from "../types";
import {
  requireCategoryId,
  extractCategoryId,
  validateRequiredFields,
} from "../utils/dataTransform";

// çŠ¶æ€å¸¸é‡
const TOOL_STATUS = {
  ACTIVE: "active",
  INACTIVE: "inactive",
};

// å·¥å…·è¾“å…¥ç±»å‹ï¼ˆç”¨äºåˆ›å»ºå’Œæ›´æ–°ï¼‰ï¼Œä½¿ç”¨è›‡å½¢å‘½åä»¥åŒ¹é…æ•°æ®åº“å­—æ®µ
interface ToolInput {
  name: string;
  description: string;
  url: string;
  category_id: string;
  icon?: string;
  is_featured?: boolean;
  status?: string;
  meta_title?: string;
  meta_description?: string;
  sort_order?: number;
  updated_at?: string; // æ·»åŠ æ›´æ–°å­—æ®µ
}

/**
 * å·¥å…·æœåŠ¡ç±»
 * æä¾›å·¥å…·æ•°æ®çš„CRUDæ“ä½œå’Œæœç´¢åŠŸèƒ½
 */
export class ToolsService {
  /**
   * è·å–æ‰€æœ‰å·¥å…·ï¼ˆä¼˜åŒ–ç‰ˆæœ¬ï¼Œå¸¦ç¼“å­˜ï¼‰
   * @param {SearchFilters} [filters] - æœç´¢è¿‡æ»¤å™¨
   * @returns {Promise<SearchResult<Tool>>} æœç´¢ç»“æœ
   */
  static async getTools(filters?: SearchFilters): Promise<SearchResult<Tool>> {
    // ç”Ÿæˆç¼“å­˜é”®
    const cacheKey = `tools_${JSON.stringify(filters || {})}`;
    
    // ä½¿ç”¨ç¼“å­˜è£…é¥°å™¨
    return withCache(
      this._getToolsFromAPI.bind(this),
      () => cacheKey,
      apiCache,
      2 * 60 * 1000 // 2åˆ†é’Ÿç¼“å­˜
    )(filters);
  }

  // å†…éƒ¨æ–¹æ³•ï¼Œä¸ä½¿ç”¨ç¼“å­˜
  private static async _getToolsFromAPI(filters?: SearchFilters): Promise<SearchResult<Tool>> {
    try {
      let query = supabase
        .from(TABLES.TOOLS)
        .select(
          `
          *,
          category:categories(*)
        `,
        )
        .eq("status", TOOL_STATUS.ACTIVE);

      // åº”ç”¨æœç´¢è¿‡æ»¤å™¨
      if (filters?.query) {
        query = query.or(
          `name.ilike.%${filters.query}%,description.ilike.%${filters.query}%`,
        );
      }

      if (filters?.category && filters.category !== "all") {
        query = query.eq("category_id", filters.category);
      }

      // æ’åº
      const sortBy = filters?.sortBy || "sort_order";
      const sortOrder = filters?.sortOrder || "asc";
      query = query.order(sortBy, { ascending: sortOrder === "asc" });

      // åˆ†é¡µ
      const page = filters?.page || 1;
      const limit = filters?.limit || 20;
      const offset = (page - 1) * limit;
      query = query.range(offset, offset + limit - 1);

      const { data, error, count } = await query;

      if (error) {
        throw new Error(handleSupabaseError(error));
      }

      // è½¬æ¢æ•°æ®æ ¼å¼
      const tools: Tool[] = (data || []).map(this.transformToolRow);

      return {
        items: tools,
        total: count || 0,
        page,
        limit,
        hasMore: (count || 0) > offset + limit,
      };
    } catch (error) {
      const appError = ErrorHandler.handleApiError(error);
      ErrorHandler.logError(appError, "ToolsService.getTools");
      throw appError;
    }
  }

  // è·å–å•ä¸ªå·¥å…·ï¼ˆä¼˜åŒ–ç‰ˆæœ¬ï¼Œå¸¦ç¼“å­˜ï¼‰
  static async getTool(id: string): Promise<Tool> {
    const cacheKey = `tool_${id}`;
    
    return withCache(
      this._getToolFromAPI.bind(this),
      () => cacheKey,
      apiCache,
      5 * 60 * 1000 // 5åˆ†é’Ÿç¼“å­˜
    )(id);
  }

  // å†…éƒ¨æ–¹æ³•
  private static async _getToolFromAPI(id: string): Promise<Tool> {
    try {
      const { data, error } = await supabase
        .from(TABLES.TOOLS)
        .select(
          `
          *,
          category:categories(*)
        `,
        )
        .eq("id", id)
        .single();

      if (error) {
        throw new Error(handleSupabaseError(error));
      }

      return this.transformToolRow(data);
    } catch (error) {
      const appError = ErrorHandler.handleApiError(error);
      ErrorHandler.logError(appError, "ToolsService.getTool");
      throw appError;
    }
  }

  /**
   * åˆ›å»ºæ–°å·¥å…·
   * @param {ToolInput} toolData - å·¥å…·æ•°æ®
   * @returns {Promise<Tool>} æ–°åˆ›å»ºçš„å·¥å…·
   */
  static async createTool(toolData: ToolInput): Promise<Tool> {
    try {
      // éªŒè¯å¿…éœ€å­—æ®µ
      validateRequiredFields(toolData, ["name", "description", "url"], "Tool");

      // éªŒè¯å¹¶æå–åˆ†ç±» ID
      const categoryId = requireCategoryId(toolData);

      const { data, error } = await supabase
        .from(TABLES.TOOLS)
        .insert({
          name: toolData.name,
          description: toolData.description,
          url: toolData.url,
          category_id: categoryId,
          icon: toolData.icon,
          is_featured: toolData.is_featured || false,
          status: TOOL_STATUS.ACTIVE,
          meta_title: toolData.meta_title,
          meta_description: toolData.meta_description,
          sort_order: toolData.sort_order || 0,
        })
        .select(
          `
          *,
          category:categories(*)
        `,
        )
        .single();

      if (error) {
        throw new Error(handleSupabaseError(error));
      }

      const tool = this.transformToolRow(data);
      
      // æ¸…ç†ç›¸å…³ç¼“å­˜
      this.clearRelatedCache();
      
      return tool;
    } catch (error) {
      const appError = ErrorHandler.handleApiError(error);
      ErrorHandler.logError(appError, "ToolsService.createTool");
      throw appError;
    }
  }

  /**
   * æ›´æ–°å·¥å…·
   * @param {string} id - å·¥å…·ID
   * @param {Partial<ToolInput>} toolData - è¦æ›´æ–°çš„å·¥å…·æ•°æ®
   * @returns {Promise<Tool>} æ›´æ–°åçš„å·¥å…·
   */
  static async updateTool(
    id: string,
    toolData: Partial<ToolInput>,
  ): Promise<Tool> {
    try {
      const updateData: Partial<ToolInput> = {};

      if (toolData.name) updateData.name = toolData.name;
      if (toolData.description) updateData.description = toolData.description;
      if (toolData.url) updateData.url = toolData.url;

      // å¤„ç†åˆ†ç±» ID - æ”¯æŒå¤šç§æ ¼å¼
      const categoryId = extractCategoryId(toolData);
      if (categoryId) {
        updateData.category_id = categoryId;
      }

      if (toolData.icon !== undefined) updateData.icon = toolData.icon;
      if (toolData.is_featured !== undefined)
        updateData.is_featured = toolData.is_featured;
      if (toolData.status) updateData.status = toolData.status;
      if (toolData.meta_title !== undefined)
        updateData.meta_title = toolData.meta_title;
      if (toolData.meta_description !== undefined)
        updateData.meta_description = toolData.meta_description;
      if (toolData.sort_order !== undefined)
        updateData.sort_order = toolData.sort_order;

      updateData.updated_at = new Date().toISOString();

      const { data, error } = await supabase
        .from(TABLES.TOOLS)
        .update(updateData)
        .eq("id", id)
        .select(
          `
          *,
          category:categories(*)
        `,
        )
        .single();

      if (error) {
        throw new Error(handleSupabaseError(error));
      }

      const tool = this.transformToolRow(data);
      
      // æ¸…ç†ç›¸å…³ç¼“å­˜
      this.clearRelatedCache(id);
      
      return tool;
    } catch (error) {
      console.error("Error updating tool:", error);
      throw error;
    }
  }

  // åˆ é™¤å·¥å…·
  static async deleteTool(id: string): Promise<void> {
    try {
      const { error } = await supabase.from(TABLES.TOOLS).delete().eq("id", id);

      if (error) {
        throw new Error(handleSupabaseError(error));
      }
      
      // æ¸…ç†ç›¸å…³ç¼“å­˜
      this.clearRelatedCache(id);
    } catch (error) {
      console.error("Error deleting tool:", error);
      throw error;
    }
  }

  // å¢åŠ ç‚¹å‡»æ¬¡æ•°ï¼ˆåŸå­æ“ä½œï¼‰
  static async incrementClickCount(id: string): Promise<void> {
    try {
      const { error } = await supabase.rpc("increment_click_count", {
        tool_id: id,
      });

      if (error) {
        throw new Error(handleSupabaseError(error));
      }
      
      // æ¸…ç†ç›¸å…³ç¼“å­˜
      this.clearRelatedCache(id);
    } catch (error) {
      console.error("Error incrementing click count:", error);
      throw error;
    }
  }

  // æ›´æ–°æ”¶è—çŠ¶æ€
  static async updateFavoriteStatus(
    id: string,
    isFavorite: boolean,
  ): Promise<void> {
    try {
      const { error } = await supabase
        .from(TABLES.TOOLS)
        .update({
          is_favorite: isFavorite,
          updated_at: new Date().toISOString(),
        })
        .eq("id", id);

      if (error) {
        throw new Error(handleSupabaseError(error));
      }
      
      // æ¸…ç†ç›¸å…³ç¼“å­˜
      this.clearRelatedCache(id);
    } catch (error) {
      console.error("Error updating favorite status:", error);
      throw error;
    }
  }

  // è·å–çƒ­é—¨å·¥å…·ï¼ˆä¼˜åŒ–ç‰ˆæœ¬ï¼Œå¸¦ç¼“å­˜ï¼‰
  static async getPopularTools(limit = 10): Promise<Tool[]> {
    const cacheKey = `popular_tools_${limit}`;
    
    return withCache(
      this._getPopularToolsFromAPI.bind(this),
      () => cacheKey,
      apiCache,
      5 * 60 * 1000 // 5åˆ†é’Ÿç¼“å­˜
    )(limit);
  }

  // å†…éƒ¨æ–¹æ³•
  private static async _getPopularToolsFromAPI(limit = 10): Promise<Tool[]> {
    try {
      const { data, error } = await supabase
        .from(TABLES.TOOLS)
        .select(
          `
          *,
          category:categories(*)
        `,
        )
        .eq("status", TOOL_STATUS.ACTIVE)
        .order("click_count", { ascending: false })
        .limit(limit);

      if (error) {
        throw new Error(handleSupabaseError(error));
      }

      return (data || []).map(this.transformToolRow);
    } catch (error) {
      console.error("Error fetching popular tools:", error);
      throw error;
    }
  }

  // è·å–æ¨èå·¥å…·ï¼ˆä¼˜åŒ–ç‰ˆæœ¬ï¼Œå¸¦ç¼“å­˜ï¼‰
  static async getFeaturedTools(limit = 6): Promise<Tool[]> {
    const cacheKey = `featured_tools_${limit}`;
    
    return withCache(
      this._getFeaturedToolsFromAPI.bind(this),
      () => cacheKey,
      apiCache,
      10 * 60 * 1000 // 10åˆ†é’Ÿç¼“å­˜ï¼ˆæ¨èå·¥å…·å˜åŒ–è¾ƒå°‘ï¼‰
    )(limit);
  }

  // å†…éƒ¨æ–¹æ³•
  private static async _getFeaturedToolsFromAPI(limit = 6): Promise<Tool[]> {
    try {
      const { data, error } = await supabase
        .from(TABLES.TOOLS)
        .select(
          `
          *,
          category:categories(*)
        `,
        )
        .eq("status", TOOL_STATUS.ACTIVE)
        .eq("is_featured", true)
        .order("sort_order", { ascending: true })
        .limit(limit);

      if (error) {
        throw new Error(handleSupabaseError(error));
      }

      return (data || []).map(this.transformToolRow);
    } catch (error) {
      console.error("Error fetching featured tools:", error);
      throw error;
    }
  }

  // æœç´¢å·¥å…·
  static async searchTools(query: string, limit = 20): Promise<Tool[]> {
    try {
      const { data, error } = await supabase
        .from(TABLES.TOOLS)
        .select(
          `
          *,
          category:categories(*)
        `,
        )
        .eq("status", TOOL_STATUS.ACTIVE)
        .or(`name.ilike.%${query}%,description.ilike.%${query}%`)
        .order("click_count", { ascending: false })
        .limit(limit);

      if (error) {
        throw new Error(handleSupabaseError(error));
      }

      return (data || []).map(this.transformToolRow);
    } catch (error) {
      console.error("Error searching tools:", error);
      throw error;
    }
  }

  // åœ¨æ›´æ–°ã€åˆ›å»ºã€åˆ é™¤æ“ä½œåæ¸…ç†ç›¸å…³ç¼“å­˜
  private static clearRelatedCache(toolId?: string): void {
    // æ¸…ç†åˆ—è¡¨ç¼“å­˜
    apiCache.clear(); // ç®€å•ç²—æš´çš„æ–¹å¼ï¼Œå®é™…ä¸­å¯ä»¥æ›´ç²¾ç»†åŒ–
    
    // å¦‚æœæœ‰å…·ä½“çš„ toolIdï¼Œæ¸…ç†è¯¥å·¥å…·çš„ç¼“å­˜
    if (toolId) {
      apiCache.delete(`tool_${toolId}`);
    }
  }

  // è½¬æ¢æ•°æ®åº“è¡Œä¸ºä¸šåŠ¡å¯¹è±¡
  private static transformToolRow(row: any): Tool {
    return {
      id: row.id,
      name: row.name,
      description: row.description,
      url: row.url,
      icon: row.icon,
      category_id: row.category_id,
      tags: [], // TODO: å®ç°æ ‡ç­¾å…³è”
      is_favorite: row.is_favorite,
      click_count: row.click_count,
      is_featured: row.is_featured,
      status: row.status,
      created_at: row.created_at,
      updated_at: row.updated_at,
      created_by: row.created_by,
      meta_title: row.meta_title,
      meta_description: row.meta_description,
      sort_order: row.sort_order,
    };
  }

  // è½¬æ¢åˆ†ç±»æ•°æ®åº“è¡Œä¸ºä¸šåŠ¡å¯¹è±¡ (è¢«CategoriesServiceè°ƒç”¨)
  // å·²ç§»é™¤æœªä½¿ç”¨çš„ transformCategoryRow
}
